<template>
  <div class="calendar">
    <span class="cubeic-back" @click="prev" />
    <em @click="showDatePicker">{{ `${nowDate.getFullYear()}-${nowDate.getMonth() + 1}-${nowDate.getDate()}` }}</em>
    <span class="cubeic-arrow" @click="next" />
  </div>
</template>

<script>
export default {
  props: {
    startDate: {
      type: Date,
      default: null
    },
    endDate: {
      type: Date,
      default: null
    },
    nowDate: {
      type: Date,
      default: null
    }
  },
  methods: {
    showDatePicker () {
      if (!this.datePicker) {
        this.datePicker = this.$createDatePicker({
          title: '日期选择器',
          min: this.startDate,
          max: this.endDate,
          value: this.nowDate,
          onSelect: this.selectHandle,
          onCancel: this.cancelHandle
        })
      } else {
        this.datePicker.$updateProps({
          value: this.nowDate
        })
      }

      this.datePicker.show()
    },
    selectHandle (date, selectedVal, selectedText) {
      this.$emit('selectedDate', selectedVal)
    },
    prev () {
      this.$emit('prevDay')
    },
    next () {
      this.$emit('nextDay')
    }
  }
}
</script>

<style lang="stylus" scoped>
  .calendar {
    position: fixed;
    top: 40px;
    left: 0;
    right: 0;
    padding: 0 15%;
    display: flex;
    background-color: #f3f3f3;
    box-shadow: 0 3px 3px #ccc;
    z-index: 1;
    span {
      width: 60px;
      line-height: 40px;
      font-size: 25px;
      color: #c30e2f;
      text-align: center;
    }
    em {
      flex: 1;
      line-height: 40px;
      text-align: center;
    }
  }
</style>



