<template>
  <div class="order-manage">
    <tab-menu :tab-index="tabIndex" :tab-menu-txt="tabMenuTxt" @tab="tab" />

    <calendar ref="calendar" :start-date="startDate" :end-date="endDate" :now-date="nowDate" @selectedDate="selectedDate" @prevDay="prevDay" @nextDay="nextDay" />

    <cube-scroll :data="data" class="wrapper">
      <div class="content">
        <div v-for="item in data" :key="item" class="item">
          <div class="tit1">
            <em class="seat-num">C001</em>
            <em class="status">待出餐</em>
          </div>
          <div class="tit2">
            <em>预约订单</em>
            <em class="order-time">2018-12-10 18:20</em>
          </div>
          <div class="tit3">
            <em>共3件商品</em>
            <em class="pay">总计￥120元</em>
          </div>
          <div class="goods">
            <div class="info">
              <p class="name">一颗柠檬茶</p>
              <p class="attr">大杯/冰/无/无糖</p>
            </div>
            <em class="sum">X1</em>
            <em class="price">￥120.0</em>
          </div>
          <div class="goods">
            <div class="info">
              <p class="name">一颗柠檬茶</p>
              <p class="attr">大杯/冰/无/无糖</p>
            </div>
            <em class="sum">X1</em>
            <em class="price">￥120.0</em>
          </div>
          <div class="goods">
            <div class="info">
              <p class="name">一颗柠檬茶</p>
              <p class="attr">大杯/冰/无/无糖</p>
            </div>
            <em class="sum">X1</em>
            <em class="price">￥120.0</em>
          </div>
          <p class="order-num">订单号：f0999999999</p>
          <div class="bar-code">
            <img src="" alt="barCode">
          </div>
          <p class="create-time">下单时间：2018-08-10 17:20</p>
          <p class="tel">联系方式：18878787845</p>
          <div class="btn-box">
            <span>打印</span>
            <span>退单</span>
            <span>确认出餐</span>
          </div>
        </div>
      </div>
    </cube-scroll>

    <my-menu />
  </div>
</template>
<script>
import myMenu from '@/components/menu'
import tabMenu from '@/components/tabMenu'
import calendar from '@/components/calendar'

const d = new Date()

export default {
  components: {
    calendar,
    tabMenu,
    myMenu
  },
  data () {
    return {
      tabIndex: 0,
      tabMenuTxt: ['全部', '待出餐', '已出餐', '已退单'],
      startDate: new Date(2018, 0, 1),
      endDate: new Date(d.getFullYear(), d.getMonth(), d.getDate()),
      nowDate: d,
      data: [1, 3, 4, 5]
    }
  },
  methods: {
    tab (index) {
      this.tabIndex = index
    },
    selectedDate (val) {
      this.nowDate = new Date(val[0], val[1] - 1, val[2])
    },
    prevDay () {
      if (this.nowDate.getTime() > this.startDate.getTime()) {
        this.dateCount('prev')
      } else {
        this.$createToast({
          time: 1000,
          type: 'warn',
          txt: '亲不能再往前点了哦'
        }).show()
      }
    },
    nextDay () {
      if (this.nowDate.getTime() < this.endDate.getTime()) {
        this.dateCount('next')
      } else {
        this.$createToast({
          time: 1000,
          type: 'warn',
          txt: '亲不能再往后点了哦'
        }).show()
      }
    },
    dateCount (type) {
      const sum = 24 * 60 * 60 * 1000
      const nowT = this.nowDate.getTime()

      if (type === 'prev') {
        this.nowDate = new Date(nowT - sum)
      } else {
        this.nowDate = new Date(nowT + sum)
      }
    }
  }
}
</script>

<style lang="stylus">
  .pub1 {
    display: flex;
    padding: 0 25px;
    border-bottom: 1px solid #dfdfdf;
  }
  .order-manage {
    .wrapper {
      position: fixed;
      top: 80px;
      bottom: 40px;
      width: 100%;
      height: inherit;
      .content {
        .item {
          .tit1 {
            @extend .pub1;
            border-top: 1px solid #dfdfdf;
            em {
              line-height: 70px;
              &:first-child {
                flex: 1;
                font-size: 30px;
                font-weight: bold;
              }
              &:last-child {
                color: #c30e2f;
                padding-left: 25px;
                border-left: 1px solid #dfdfdf;
              }
            }
          }
          .tit2, .tit3 {
            @extend .pub1;
            line-height: 35px;
            em {
              flex: 1;
              &:last-child {
                text-align: right;
              }
              &.order-time {
                color: #c30e2f;
              }
              &.pay {
                border-left: 1px solid #dfdfdf;
              }
            }
          }
          .goods {
            @extend .pub1;
            padding: 10px 25px;
            .info {
              flex: 1;
              .attr {
                padding-top: 8px;
                font-size: 12px;
                color: #666;
              }
            }
            .sum, .price {
              line-height: 35px;
            }
            .price {
              width: 80px;
              text-align: right;
            }
          }
          .order-num {
            @extend .pub1;
            line-height: 35px;
          }
          .bar-code {
            height: 100px;
            margin-bottom: 10px;
            border-bottom: 1px solid #dfdfdf;
          }
          .create-time, .tel {
            padding: 0 25px;
            line-height: 25px;
          }
          .btn-box {
            @extend .pub1;
            border: none;
            border-top: 1px solid #dfdfdf;
            padding: 15px 25px;
            margin: 10px -4% 20px 0;
            span {
              width: 30%;
              line-height: 35px;
              border: 1px solid #000;
              border-radius: 5px;
              margin-right: 5%;
              text-align: center;
            }
          }
        }
      }
    }
  }
</style>



