<template>
  <div class="tab-menu">
    <span v-for="(item, index) in tabMenuTxt" :key="index" :class="tabIndex === index ? 'cur' : ''" @click="tab(index)">{{ item }}</span>
  </div>
</template>

<script>
export default {
  props: {
    tabIndex: {
      type: Number,
      default: 0
    },
    tabMenuTxt: {
      type: Array,
      default: null
    }
  },
  methods: {
    tab (index) {
      this.$emit('tab', index)
    }
  }
}
</script>

<style lang="stylus" scoped>
.tab-menu {
  position: fixed;
  width: 100%;
  display: flex;
  font-size: 13px;
  border-bottom: 1px solid #ccc;
  z-index: 1;
  span {
    display: block;
    width: 25%;
    text-align: center;
    line-height: 39px;
    color: #898989;
    background-color: #f3f3f3;
    &.cur {
      color: #000;
      background-color: #fff;
    }
  }
}
</style>




